body {
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  display: flex;
}

main {
  background-color: red;
  flex: 1;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  display: block;
}

button {
  cursor: pointer;
}

.link {
  text-decoration: none;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

header {
  background-color: #ffea00;
}

.header-div {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.a-logo {
  align-items: center;
  display: flex;
}

.img-logo-header {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.header-button {
  fill: #000;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  padding: 0;
  transition: fill .25s cubic-bezier(.47, 0, .745, .715);
}

.header-button:hover, .header-button:focus {
  fill: #adff2f;
}

.is-open {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.close-menu {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border: 2px solid #ffea00;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: fill .25s cubic-bezier(.455, .03, .515, .955), border .25s cubic-bezier(.455, .03, .515, .955);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.close-menu:hover, .close-menu:focus {
  fill: #ffea00;
  border: none;
}

.mobile-version {
  min-width: 100%;
  min-height: 100%;
  z-index: 2;
  background-color: #fff;
  flex-direction: column;
  margin: 0;
  padding: 0 0 0 15px;
  transition: opacity .25s cubic-bezier(.455, .03, .515, .955), visibility .25s cubic-bezier(.455, .03, .515, .955);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.current {
  color: #000;
  position: relative;
  bottom: -1px;
}

.list-menu-header {
  width: 90%;
  margin-top: 25px;
}

.item-menu-header {
  color: #000;
  letter-spacing: .72px;
  padding: 0;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.11;
  transition: color .25s cubic-bezier(.455, .03, .515, .955);
  display: block;
}

.item-menu-header:hover, .item-menu-header:focus {
  color: #ffea00;
}

.nav {
  flex: 1;
}

.social-address-modal {
  padding-bottom: 15px;
}

.selects {
  justify-content: space-around;
  display: flex;
}

footer {
  background-color: #ffea00;
}

.footer-conteiner {
  max-width: 430px;
  margin: 15px 30px;
}

.logo-menu {
  justify-content: space-between;
  display: flex;
}

.img-logo-footer {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.list-menu-footer {
  margin: 0;
}

.item-menu-footer {
  color: #000;
  letter-spacing: .72px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
  transition: color .25s cubic-bezier(.455, .03, .515, .955);
}

.item-menu-footer:hover, .item-menu-footer:focus {
  color: #9acd32;
}

.selected-item {
  color: orange;
}

.list-icons-footer {
  gap: 10px;
  display: flex;
}

.link-footer {
  fill: #000;
  transition: fill .25s cubic-bezier(.455, .03, .515, .955);
}

.svg-footer {
  width: 30px;
  height: 30px;
}

.link-footer:hover, .link-footer:focus {
  fill: #adff2f;
}

.link-address {
  color: #000;
  letter-spacing: .72px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
}

@media (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 1000px;
    padding: 0;
  }

  .modal-container {
    padding: 0;
  }

  .mobile-version {
    max-width: 100%;
    width: 100%;
    min-width: 0;
    background-color: rgba(0, 0, 0, 0);
    flex-direction: row;
    padding: 0;
    position: static;
  }

  .is-open {
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  .open-menu, .close-menu {
    width: 1px;
    height: 1px;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    margin: -1px;
    padding: 0;
    display: none;
    position: absolute;
    overflow: hidden;
  }

  .a-logo {
    margin-left: 50px;
  }

  .img-logo-header {
    width: 50px;
    height: 50px;
  }

  .nav {
    min-width: 100%;
  }

  .item-menu-header:hover, .item-menu-header:focus {
    color: #9acd32;
  }

  .current:after {
    content: "";
    width: 100%;
    height: 4px;
    background: orange;
    border-radius: 2px;
    position: absolute;
    bottom: -1px;
    left: 0;
  }

  .header-div {
    justify-content: center;
  }

  .list-menu-header {
    justify-content: center;
    gap: calc(50% - 300px);
    display: flex;
  }

  .mobile-version {
    overflow: hidden;
  }

  .social-address-modal {
    width: 1px;
    height: 1px;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    border: 0;
    margin: -1px;
    padding: 0;
    position: absolute;
    overflow: hidden;
  }

  footer {
    justify-content: center;
    display: flex;
  }

  .footer-conteiner {
    width: 100%;
    max-width: 800px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .logo-menu {
    width: 100%;
    max-width: 480px;
    justify-content: space-between;
    align-items: center;
  }

  .img-logo-footer {
    width: 90px;
    height: 90px;
  }

  .list-menu-footer {
    display: block;
  }
}

